import { CSVToArray } from "./utils.js"

import Chart from "chart.js/auto";

import 'chartjs-adapter-date-fns';
import { de } from 'date-fns/locale';
import annotationPlugin from 'chartjs-plugin-annotation';

import atbUrl from 'url:./atb.txt'
import csvFileUrl from 'url:./temp.csv'


(async function() {
  Chart.register(annotationPlugin);

  const data = await getTemp();

  new Chart(
    document.getElementById("main"),
    {
      type: "line",
      options: {
        plugins: {
          annotation: {
            annotations: {
              "38": line38(),
              ...(await getAtb()),
            }
          }
        },
        scales: {
          y: {
            min: 36,
            max: 43,
            ticks: {
              callback: value => `${value} C`
            }
          },
          y1: {
            type: 'linear',
            display: true,
            min: 8,
            max: 300
          },
          x: {
            min: "2023-01-07",
            adapters: {
              date: { locale: de }
            },
            type: 'time',
          }
        }
      },
      data: {
        labels: data.map((row) => row.year),
        datasets: [
          {
            label: "temperature",
            data: data.map((row) => row.count),
          },
          {
            label: "crp",
            data: data.map(_ => 100 + Math.random() * 10),
            yAxisID: "y1",
          }
        ],
      },
    },
  );
})()

function line38() {
  return {
    label: { content: "38", display: true },
    type: "box",
    yMin: 38,
    yMax: 38,
    backgroundColor: 'rgba(255, 99, 132, 0.25)'
  }
}

async function getAtb() {
  const data = await fetch(atbUrl).then(r => r.json())

  return data.map(atb => {
    return {
      label: { content: atb.title, display: true },
      type: "box",
      xMin: atb.startDate,
      xMax: atb.endDate,
      yMin: 41 + (atb.offsetY || 0),
      yMax: 41.5 + (atb.offsetY || 0),
      backgroundColor: 'rgba(255, 99, 132, 0.25)'
    }
  })
}

async function getTemp() {
  const data = await fetch(csvFileUrl).then(r => r.text())
  const csv = CSVToArray(data.trim())

  return csv.slice(1).map(e => { return { year: e[0], count: parseFloat(e[1]) } })
}

